@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,400&display=swap);
:root{
  /* colors */
--white:#ffffff;
--dark-blue:#28527A;
--light-blue:#8AC4D0;
--dark-yelow:#F4D160;
--ligth-yelow:#FBEEAC;
--white:#ffffff;
--black:#000000;
 /* fonst size */
--normal-font:12px;

}

html, body, #root {
  padding: 0;
  margin: 0;
  font-size: 12px;
  font-size: var(--normal-font);
  width: 100vw;
  height: 100vh;

}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* buscador por lugares */
.search {
  position: absolute;
  top: 1rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 100%;
  max-width: 400px;
  z-index: 10;
  text-align: center;
}

.search input {
  padding: 0.5rem;
  font-size: 1.5rem;
  width: 100%;
}
/* Buscador coordenadas  */
.searchCoordinate {
  position: absolute;
  top: 1rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 100%;
  max-width: 400px;
  z-index: 10;
  text-align: center;
  display: -webkit-flex;
  display: flex;

}
.searchCoordinate form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;

}
.searchCoordinate input {
  padding: 0.5rem;
  font-size: 1.5rem;
  width: 40%;
  margin: 5px;
  text-align: center;
}
.searchCoordinate button{
  -webkit-align-self: center;
          align-self: center;
  text-align: center;
  background-color: var(--dark-blue);
  color: var(--white);
  border: none;
  height: 30px;
  padding: 7px;
  font-size: 1.2rem;
  width: 90%;
  
}


.locate {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  z-index: 15;
}
.locate img {
  width: 30px;
  cursor: pointer;
}
